<template>
  <div class="main_navs">
    <Header></Header>
    <div class="container">
      <div class="univer_title">{{$t('uni')}}</div>
      <div class="univer_description"> {{$t('uni_title')}}</div>
      <div class="uni_btn_group" >
        <button @click="isWorld()" :class="{'active':!is_check}">
          {{ $t('war') }}

          <svg v-if="is_check" width="20" height="21" viewBox="0 0 20 21" fill="none" xmlns="http://www.w3.org/2000/svg">
            <circle cx="10" cy="10.5" r="10" fill="#D9D9D9"/>
            <path d="M15.373 6.74805L7.8125 14.3086L4.62695 11.123L3.99805 11.752L7.49805 15.252L7.8125 15.5527L8.12695 15.252L16.002 7.37695L15.373 6.74805Z" fill="#043936"/>
          </svg>

          <svg v-if="!is_check" width="20" height="21" viewBox="0 0 20 21" fill="none" xmlns="http://www.w3.org/2000/svg">
            <circle cx="10" cy="10.5" r="10" fill="#043936"/>
            <path d="M6.1582 6.5293L5.5293 7.1582L9.37109 11L5.5293 14.8418L6.1582 15.4707L10 11.6289L13.8418 15.4707L14.4707 14.8418L10.6289 11L14.4707 7.1582L13.8418 6.5293L10 10.3711L6.1582 6.5293Z" fill="white"/>
          </svg>

        </button>
        <button @click="isTech()" :class="{'active':!is_check1}">
          {{ $t('tex') }}

          <svg v-if="is_check1" width="20" height="21" viewBox="0 0 20 21" fill="none" xmlns="http://www.w3.org/2000/svg">
            <circle cx="10" cy="10.5" r="10" fill="#D9D9D9"/>
            <path d="M15.373 6.74805L7.8125 14.3086L4.62695 11.123L3.99805 11.752L7.49805 15.252L7.8125 15.5527L8.12695 15.252L16.002 7.37695L15.373 6.74805Z" fill="#043936"/>
          </svg>

          <svg v-if="!is_check1" width="20" height="21" viewBox="0 0 20 21" fill="none" xmlns="http://www.w3.org/2000/svg">
            <circle cx="10" cy="10.5" r="10" fill="#043936"/>
            <path d="M6.1582 6.5293L5.5293 7.1582L9.37109 11L5.5293 14.8418L6.1582 15.4707L10 11.6289L13.8418 15.4707L14.4707 14.8418L10.6289 11L14.4707 7.1582L13.8418 6.5293L10 10.3711L6.1582 6.5293Z" fill="white"/>
          </svg>

        </button>
        <button @click="isUzb()" :class="{'active':!is_check2}">
          {{ $t('uzbek') }}

          <svg v-if="is_check2" width="20" height="21" viewBox="0 0 20 21" fill="none" xmlns="http://www.w3.org/2000/svg">
            <circle cx="10" cy="10.5" r="10" fill="#D9D9D9"/>
            <path d="M15.373 6.74805L7.8125 14.3086L4.62695 11.123L3.99805 11.752L7.49805 15.252L7.8125 15.5527L8.12695 15.252L16.002 7.37695L15.373 6.74805Z" fill="#043936"/>
          </svg>

          <svg v-if="!is_check2" width="20" height="21" viewBox="0 0 20 21" fill="none" xmlns="http://www.w3.org/2000/svg">
            <circle cx="10" cy="10.5" r="10" fill="#043936"/>
            <path d="M6.1582 6.5293L5.5293 7.1582L9.37109 11L5.5293 14.8418L6.1582 15.4707L10 11.6289L13.8418 15.4707L14.4707 14.8418L10.6289 11L14.4707 7.1582L13.8418 6.5293L10 10.3711L6.1582 6.5293Z" fill="white"/>
          </svg>

        </button>
      </div>
      <div class="uni_f">
        <div class="uni_content" >
          <div class="row">
            <div class="col-xl-4" v-for="(item,index) in $store.state.universities.results" :key="index">
              <div class="uni_card">
                <div>
                  <a :href="item.link" class="main_uni_link">
                    <div>
                      просмотр рейтинга
                    </div>
                  </a>
                </div>
                <router-link class="decoration" :to="`/universities/${item.id}`">
                  <div class="image_group">
                    <img :src="item.image_url" alt="">
                    <div class="uni_position">
                      <div class="uni_card_title">
                        {{ item.title }}
                      </div>
                      <div class="absolute_text">
                        {{ item.short_description }}
                      </div>
                    </div>
                  </div>
                </router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="pagenationss" v-if="$store.state.universities.results">
        <b-pagination
            v-model="page"
            :total-rows="$store.state.universities.total_pages"
            :per-page="perPage"
            first-number
            last-number
            aria-controls="my-table"></b-pagination>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "../components/Header";
import Footer from "../components/Footer";

export default {
  components: {Footer, Header},
  mounted() {
    this.$store.dispatch('getUniversity', {
      'type':'is_world=1',
      'page':this.page
    })
    this.is_check=false
  },
  data(){
    return{
      page: 1,
      total: 2,
      perPage:1,
      is_check:true,
      is_check1:true,
      is_check2:true,
      type:1
    }
  },
  methods:{
    isWorld(){
      this.$store.dispatch('getUniversity',{
        'type':'is_world=1',
        'page':this.page
      })
      this.is_check=false
      this.is_check2=true
      this.is_check1=true
      this.type='is_world=1'
    },
    isTech(){
      this.$store.dispatch('getUniversity',{
        'type':'is_prof_education=1',
        'page':this.page
      })
      this.is_check=true
      this.is_check1=false
      this.is_check2=true
      this.type='is_prof_education=1'
    },
    isUzb(){
      this.$store.dispatch('getUniversity',{
        'type':'is_country=1',
        'page':this.page
      })
      this.is_check=true
      this.is_check1=true
      this.is_check2=false
      this.type='is_country=1'
    }
  },
  watch: {
    page(page) {
      this.$store.dispatch('getUniversity', {
        'type':'is_country=1',
        'page':page
      })
      window.scrollTo(0,0);
    },
  },
}
</script>
